@use "@scss" as global;
.face {
  border-radius: 10px;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;

  filter: drop-shadow(0 12.5px 100px rgba(50, 50, 73, 0.4))
    drop-shadow(0 10px 10px rgba(50, 50, 73, 0.3));
}

.back {
  overflow: hidden;
  background-color: rgb(255, 255, 255, 0);
  display: block;
  width: 100%;
  height: 100%;
  text-align: center;
  inset: 0;
  align-items: center;
  display: flex;
  justify-content: center;
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
  box-shadow: 0 0 0 #aaa;
}
